.left-nav {
    background: rgb(56, 53, 53);

}

.folders {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-top: var(--size-large);
    color: lightgray;
}

.level1 {
    display: flex;
    font-size: small;
    margin: 0 var(--size-huge) 0 var(--size-medium);
}

.level2 {
    display: flex;
    font-size: small;
    margin: var(--size-tiny) var(--size-huge) 0 var(--size-largeish);
    align-items: center;
}

svg {
    margin-right: var(--size-tiny);
}